.NavBar{
    background: #181818;
    height: 80px;
    width: 100%;
    position: fixed;
    z-index: 999;
    opacity: 0.9;
    display: flex;
    justify-content: center;
    align-items: center;
}
.NavBar ul{
    list-style: none;
    padding: 0%;
    margin: 0%; 
}
.NavBar ul li{
    display: inline-block;
    margin: 20px;
}
.NavBar ul li a{
    text-decoration: none;
    color: #fff;
}
.NavBar a:hover{
    border-bottom:5px solid yellow;
}

@media screen and (max-width: 600px) {  
    .NavBar{
        background: rgb(107, 107, 4);
        height: 80px;
        width: 100%;
        position: fixed;
        z-index: 999;
       opacity: 5;
       
    }
    
    .NavBar ul{
        list-style: none;
        padding: 0%;
        margin: 0%; 
    }
    .NavBar ul li{
        display: inline-block;
        margin: 10px;
    }
    .NavBar ul li a{
        text-decoration: none;
        color: #fff;
    }
    .NavBar a:hover{
        color: #181818;
        font-weight: 900;
    }
}