.About{
    background: #181818;
    padding-bottom: 5%;
}
.About .Container .Photo .Binary{
    height: 500px;
}
.About .Container .Photo{
    display: flex;
    width: 50%;
    justify-content: center;
}
.About .Container{
    display: flex;
}
.About .Container .Text{
    width: 47%;
    margin-left: 3%;
}
.About .Container .Text h2{
    font-weight: 900;
    font-size: 46;
}
.About .Container .Text p{
    font-size: 20px;
}

@media screen and (max-width: 600px) {  
    .about{
        background: #181818;
        padding-bottom: 5%;
    }
    .About .Container .Photo .Binary{
        height: 300px;
        margin-top: 30%;
    }
    .About .Container .Photo{
        display: flex;
        width: 50%;
        justify-content: center;
    }
    .About .Container{
        display: flex;
        flex-direction: row;
    }
    .About .Container .Text{
        width: 47%;
        margin-left: 2%;
        font-size: 5px;
        justify-content: center;
    }
    .About .Container .Text h1{
        font-weight: 100;
        font-size: 24px;
    }
    p{
        font-size: 18px;
    }
}        


@media screen and (max-width: 770px) {  
    .about{
        background: #181818;
        padding-bottom: 5%;
    }
