.Thumbnail img{
    width: 50%;
    height: 500px;
    
}
.Projects{
    background: #141313;
}
.Paragraph p{
    font-size: 20px;
}
.Projects .Paragraph{
    width: 65%;
    text-align: center;
    margin-left: 17%;
}
.Projects .photoComtainer .Thumbnail img{
    max-width: 100%;
 /*   margin-left: 2%;
    margin-right: 10%;  */
    border-radius: 10px;
}
.Projects .Content .Album{
    display: flex;
    flex-direction: column;
    margin: 5%; 
    height: 300px;
    align-items: center;
}

@media screen and (max-width: 600px) {  
    .Thumbnail img{
        width: 50%;
        height: 500px;
        
    }
    .Projects{
        background: #141313;
    }
    .Text p{
        padding-bottom: 2%;
        text-align-last: center;
    }
    .Projects .Paragraph{
        width: 65%;
        text-align: center;
        margin-left: 17%;
        font-size: 14px;
    }
    .Projects .photoContainer .Thumbnail img{
        width: 100%;
     /*   margin-left: 2%;
        margin-right: 10%;  */
        border-radius: 10px;
    }
    .Projects .Content .Album{
        display: flex;
        flex-direction: column;
        margin: 5%; 
        height: 100px;
        align-items: center;
    }
}    


