.Contact{
    background: #222;
    height: 100px;
    align-items: center;
    margin-top: -2%;
    padding: 10px;
}
p{
    font-size: 12px;
}
.Contact .Context{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Contact .Context svg path{
    cursor: pointer;
}


@media screen and (max-width: 600px) {  
    .Contact{
        background: #222;
        height: 100px;
        align-items: center;
        margin-top: -4%;
    }
    .Contact p{
        font-size: 8px;
        margin-bottom: 0%;
    }
    .Contact .Context{
        width: 100%;
        display: flex;
        padding: 15px;
        justify-content: center;
        align-items: center;
    }
    .Contact .Context svg path{
        cursor: pointer;
    }
}    


@media screen and (max-width: 770px) {  
    .Contact{
        background: #222;
        height: 100px;
        align-items: center;
        margin-top: -4%;
    }
    .Contact p{
        font-size: 6px;
    }
    .Contact .Context{
        width: 100%;
        display: flex;
        padding: 15px;
        justify-content: center;
        align-items: center;
        margin-top: 3%;
    }
    .Contact .Context svg path{
        cursor: pointer;
    }
}    