.Skills{
    background: #141313;
    background-position: top;
    background-size: contain;
    background: cover;
    padding-bottom: 5%;
}
.Skills p{
    padding-left: 25%;
    width: 50%;
    font-size: 20px;
}
.Skills .List ul{
    color: whitesmoke;
    list-style-type: none;
    padding: 0%;
}
.Skills .Container{
    display: flex;
    flex-wrap: wrap; 
    margin-left: 3%;
}
.Skills .Container .List{
    flex-basis: 33%;
    box-shadow:0 0 12px 4px whitesmoke;
    border-radius: 9px;
    margin-bottom: 2px;
}
.Skills .List ul li{
    display: flex;
    justify-content: center;
    font-size: 18px;
}
.Skills .Ulvi{
    width: 60%;
    margin-left: 20%;
    height: 600px;
    border-radius: 5px;
}


@media screen and (max-width: 600px) {  
    .Skills{
        background: #141313;
        background-position: top;
        background-size: contain;
        background: cover;
        padding-bottom: 5%;
    }
    .Skills p{
        padding-left: 25%;
        width: 50%;
        font-size: 18px;
    }
    .Skills .List ul{
        color: #eee;
        list-style-type: none;
        padding: 0%;
    }
    .Skills .Container{
        display: flex;
        flex-wrap: wrap; 
        margin-left: 3%;
    }
    .Skills .Container .List{
        flex-basis: 33%;
        border-radius: 9px;
        margin-bottom: 2px;
        box-shadow:0 0 6px 2px whitesmoke;
    }
    .Skills .List ul li{
        display: flex;
        justify-content: center;
        font-size: 14px;
    }
    .Skills .Ulvi{
        width: 80%;
        margin-left: 10%;
        height: 400px;
        border-radius: 9px;
    }
}    
 



