.Home{
    background-image:linear-gradient(to right, #222, #fff);
    display: flex;
    height: 630px;
    padding-top: 70px;
    position: relative;
}
.Home .Container h1{
    font: size 45px;
    line-height: 0px;
}
.Home .Container p{
    font-size: 20px;
}
.Home .Container{
    position: absolute;
    top: 25%;
    left: 13%;
}
.Home .Container .Hello{
    font-size: 63px;
    color: rgb(107, 107, 4);
}
.Home .Button{
    height: 60px;
    font-size: 25px;
    background: #222;
    color: rgb(107, 107, 4);
    border-top-left-radius: 15px;
    font-weight: bolder;
}
