.Home .Louis{
    width: 100%;
    height: 630px;
    top: 22%;
}


@media screen and (max-width: 600px) {  
    .Home{
        display: flex;
        height: 630px;
        position: relative;
       
    }
    .Home .Louis{
        width: 596px;
        height: 630px;
        top: 22%;
    }

    
    .Home .Container h1{
        font-size: 30px;
        line-height: 0px;
    }
    .Home .Container{
        position: absolute;
        top: 28%;
        left: 13%;
    }
    .Home .Container .Hello{
        font-size: 47px;
        color: rgb(107, 107, 4);
    }
}    